.achievement-heading {
    font-size: 56px;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 20px;
  }
  .achievement-subtitle {
    text-transform: uppercase;
    margin: 0px;
    margin-bottom: 50px;
  }
  .card-image {
    height: 250px;
    width: 250px;
  }
  
  .card-title {
    color: black;
    font-size: 30px;
    margin: 0px;
    font-weight: 400;
    line-height: normal;
  }
  
  .certificate-card {
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(0, 0, 0, 0.2) 0px 10px 30px -15px;
    padding: 1.5rem;
    border-radius: 10px;
    border: 1px solid rgba(211, 211, 211, 0.397);
  }
  
  .certificate-detail-div {
    text-align: center;
  }
  
  .certificate-card:hover {
    box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px -10px;
  }
  .certificate-image-div {
    text-align: center;
  }
  .achievement-cards-div {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1rem 1rem;
  }
  .certificate-card-footer {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
  }
  .certificate-card-footer > p {
    color: #333333;
    cursor: pointer;
    margin-right: 10px;
    text-align: center;
  }
  .certificate-card-footer > p:hover {
    color: #868e96;
  }
  /* Media Query */
  @media (max-width: 1380px) {
    .achievement-heading {
      font-size: 40px;
    }
  }
  @media (max-width: 768px) {
    .achievement-heading {
      font-size: 30px;
      text-align: center;
    }
    .achievement-subtitle {
      font-size: 16px;
      line-height: normal;
      text-align: center;
    }
  }
  