.contact-div-main {
    display: flex;
    justify-content: center;
}

.contact-div-main > * {
    flex: 1;
}

.contact-title {
    font-size: 56px;
    font-weight: 400;
}

.contact-detail,
.contact-detail-email {
    margin-top: 20px;
    font-size: 20px;
    font-weight: 400;
    color: #868e96;
    text-decoration: none;
}


.contact-subtitle {
    text-transform: uppercase;
}

.contact-text-div {
    margin-top: 1.5rem;
}

.contact-margin-top {
    margin-top: 4rem;
}

.contact-image-div > img {
    width: 500px;
    height: auto;
    margin-left: 1.5rem;
    margin-top: -4rem;
}

/* Media Query */
@media (max-width: 1000px), (max-width: 768px) {
    .contact-title {
        font-size: 56px;
        font-weight: 400;
        text-align: center;
    }

    .contact-title {
        font-size: 40px;
        text-align: center;
    }

    .contact-subtitle {
        font-size: 16px;
        line-height: normal;
        text-align: center;
    }

    .contact-text-div {
        text-align: center;
        margin-top: 1.5rem;
    }

    .contact-detail,
    .contact-detail-email {
        font-size: 20px;
    }

    .contact-image-div {
        display: none;
    }
}